<template>
  <div class="unit-modal">
    <a-modal
      :title="title"
      :visible="unitVisible"
      @cancel="handleCancel"
      cancelText="取消"
      okText="确定"
      :mask="false"
      :maskClosable="false"
      :width="650"
      class="modalCls"
    >
      <a-form-model ref="unitForm" :model="unitForm" :rules="unitFormRule" style="margin-bottom:20px">
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="单位名称" prop="cname">
              <a-input v-model="unitForm.cname" placeholder="请输入单位名称" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="单位别名" prop="ename">
              <a-input v-model="unitForm.ename" placeholder="请输入单位唯一别名" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="单位地址">
              <a-input v-model="unitForm.city" placeholder="请输入单位地址" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="单位联系人">
              <a-input v-model="unitForm.contact" placeholder="请输入单位联系人" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="单位电话">
              <a-input v-model="unitForm.telephone" placeholder="请输入单位电话" />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="[24,24]" type="flex" justify="center">
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="单位描述" prop="description">
              <a-textarea v-model="unitForm.description" placeholder="请输入单位描述信息" rows="4"></a-textarea>
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
      <template slot="footer">
          <a-button type="link" ghost @click="handleCancel">取消</a-button>
          <a-button type="primary" @click="handleOk">提交</a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
export default {
  name: "unit-modal",
  props: {
    // 模态框的显示与隐藏
    unitVisible: {
      type: Boolean,
      default: false,
      required: true,
    },
    // 模态框的标题
    title: {
      type: String,
      default: "添加/编辑单位",
    },
    // 表单内容 一般添加的时候才会传入
    content: {
      type: Object,
      default: function () {
        return {
          cname: "",
          ename: "",
          city: "",
          contact: "",
          telephone: "",
          description: "",
          creator: "",
          createTime: "",
          updateTime: "",
        };
      },
    },
    // 关于操作类型 0为添加 1为编辑
    optType:{
      type:Number,
      default:0,
      required: true,
    }
  },
  data() {
    return {
      unitFormRule: {
        cname: [{ required: true, message: "请输入单位名称",trigger:'blur'}],
        ename: [{ required: true, message: "请输入单位别名",trigger:'blur' }],
        city: [{ required: true, message: "请输入单位地址",trigger:'blur' }],
        contact: [{ required: true, message: "请输入单位联系人",trigger:'blur' }],
        telephone: [{ required: true, message: "请输入单位电话",trigger:'blur' }],
      },
      unitForm:{
          cname: "",
          ename: "",
          city: "",
          contact: "",
          telephone: "",
          description: "",
      }
    };
  },
  methods: {
    handleOk() {
        this.$refs.unitForm.validate(valid => {
            if(valid){
                this.$emit("unitOk", {formContent:this.unitForm,optType:this.optType});
            } else {
                this.$message.warning('输入有误，请检查');
                return false
            }
        })
    },
    handleCancel() {
      this.$emit("unitCancel", "我点击取消");
    },
  },
  watch:{
    content:{
      handler:function(newVal){
        Object.assign(this.unitForm,newVal);
      },
      deep:true
    }
  }
};
</script>
