<template>
  <div class="unit">
    <div class="tableTopTools clearfix">
      <p class="toolName fl">工具栏：</p>
      <div class="buttons fl">
        <a-button type="primary" @click="optUnit">
          <a-icon type="plus"></a-icon>添加单位
        </a-button>
      </div>
      <div class="searchInput fr">
        <a-input-search v-model="searchInput" placeholder="请输入单位名称" @change="keywordChange"></a-input-search>
      </div>
    </div>
    <div class="tableContent">
      <a-table :columns="columns" :dataSource="unitData" rowKey="id" :pagination="false">
        <span slot="gmtCreate" slot-scope="text,record">
          <span>{{record.gmtCreate | dateFormatter(1)}}</span>
        </span>
        <span slot="gmtModified" slot-scope="text,record">
          <span>{{record.gmtModified | dateFormatter(1)}}</span>
        </span>
        <span slot="action" slot-scope="text,record">
          <a class="t-optColor-normal" @click="editUnit(record)">编辑</a>
          <a class="t-optColor-warning" @click="deleteUnit(record)">删除</a>
        </span>
      </a-table>
      <div class="ar" style="margin-top:30px" v-if="unitData.length > 0">
        <a-pagination
          v-model="pageParam.page"
          :total="total"
          :page-size="pageParam.size"
          @change="pageChange"
        ></a-pagination>
      </div>
    </div>
    <unitModal
      :unitVisible="unitVisible"
      @unitOk="unitOk"
      @unitCancel="unitCancel"
      :content="content"
      :optType="optType"
    ></unitModal>
  </div>
</template>
<script>
import unitModal from "@/components/myModal/unit-modal.vue";
export default {
  name: "unit",
  components: { unitModal },
  data() {
    return {
      // 表格列
      columns: [
        {
          dataIndex: "cname",
          title: "单位名称",
        },
        {
          dataIndex: "ename",
          title: "单位别名",
        },
        {
          dataIndex: "city",
          title: "单位地址",
        },
        {
          dataIndex: "contact",
          title: "单位联系人",
        },
        {
          dataIndex: "telephone",
          title: "联系电话",
        },
        {
          dataIndex: "description",
          title: "描述",
        },
        {
          dataIndex: "creator",
          title: "创建者",
        },
        {
          dataIndex: "gmtCreate",
          title: "创建时间",
          scopedSlots: { customRender: "gmtCreate" },
        },
        {
          dataIndex: "gmtModified",
          title: "最近更新时间",
          scopedSlots: { customRender: "gmtModified" },
        },
        {
          dataIndex: "action",
          title: "操作",
          scopedSlots: { customRender: "action" },
        },
      ],
      // 表格数据
      unitData: [],
      // 操作模态框的显示 隐藏
      unitVisible: false,
      // 操作模态框的内容
      content: {
        cname: "",
        ename: "",
        city: "",
        contact: "",
        telephone: "",
        description: "",
        creator: "",
        gmtCreate: "",
        gmtModified: "",
      },
      // 模态框的操作类型 0添加 1编辑
      optType: 0,
      pageParam: {
        page: 1,
        size: 20,
      },
      searchInput: "",
      total: 0,
    };
  },
  created() {
    this.getUnit();
  },
  methods: {
    //  查询单位
    getUnit() {
      let params = Object.assign({}, this.pageParam, {
        cname: this.searchInput,
      });
      this.$API.searchUnit(params).then((res) => {
        if (res.code === 1000) {
          this.unitData = res.data;
          this.total = res.total;
          if (this.pageParam.page > 1 && this.unitData.length === 0) {
            this.pageParam.size--;
            this.getUnit();
          }
        } else {
          this.unitData = [];
        }
      });
    },
    optUnit() {
      this.resetContent()
      this.unitVisible = true;
      this.optType = 0;
    },
    //   编辑
    editUnit(column) {
      this.content = column;
      this.optType = 1;
      this.unitVisible = true;
    },
    // 删除
    deleteUnit(column) {
      const that = this;
      this.$confirm({
        title: "确定删除该条数据吗？",
        content: () => <div style="color:red">删除后将不可恢复</div>,
        okText: "确认删除",
        cancelText: "取消删除",
        onOk() {
          that.$API.delUnit(column.id).then((res) => {
            if (res.code === 1000) {
              that.$message.success("删除成功");
              that.getUnit();
            } else {
              that.$message.warning(res.message);
            }
          });
        },
      });
    },
    // 添加unit
    unitOk(val) {
      if (val.optType) {
        let param = Object.assign({}, val.formContent, { id: this.content.id });
        this.$API.editUnit(param).then((res) => {
          if (res.code === 1000) {
            this.$message.success("单位更新成功");
            this.unitVisible = false;
            this.getUnit();
          } else {
            this.$message.warning(res.message);
          }
        });
      } else {
        this.$API.addUnit(val.formContent).then((res) => {
          if (res.code === 1000) {
            this.$message.success("添加单位成功");
            this.unitVisible = false;
            this.getUnit();
          } else {
            this.$message.warning(res.message);
          }
        });
      }
    },
    // 取消unit
    unitCancel() {
      this.unitVisible = false;
      this.resetContent();
    },
    pageChange() {
      this.getUnit();
    },
    // 搜索关键词变化事件
    keywordChange() {
      this.pageParam.page = 1;
      this.getUnit();
    },
    // 清洗content
    resetContent() {
      this.content = {
        cname: "",
        ename: "",
        city: "",
        contact: "",
        telephone: "",
        description: "",
        creator: "",
        gmtCreate: "",
        gmtModified: "",
      };
    },
  },
};
</script>
<style>
.searchInput {
  width: 300px;
}
</style>